.header {
  z-index: 10;
  height: 100vh;
  background: linear-gradient(
      to bottom right,
      rgba(0, 0, 0, 0.3),
      rgba(0, 0, 0, 0.1)
    ),
    url('../images/cover_photo.jpg') no-repeat fixed center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  &__title {
    text-align: center;
    z-index: 11;
    color: white;
    font-weight: 800;
    font-size: 8rem;
  }

  &__subtitle {
    text-align: center;
    z-index: 11;
    color: white;
    font-family: Avenir, 'Helvetica Neue', Helvetica, Arial, sans-serif;
    font-size: 3rem;
  }
}

@media screen and (max-width: 768px) {
  .header {
    background-position: top;
    background-size: auto;
    &__title {
      font-size: 7rem;
    }
  }
}



